.hidden {
  display: none;
}

.live-search__container {
  position: relative;
  width: 280px;
}

.live-search__input {
  background-repeat: no-repeat;
  background-position: 8px 51%;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16"><path fill="rgb(0, 0, 0)" fill-opacity="0.4" d="M14.891,14.39l-0.5.5a0.355,0.355,0,0,1-.5,0L9.526,10.529a5.3,5.3,0,1,1,2.106-4.212,5.268,5.268,0,0,1-1.1,3.21l4.362,4.362A0.354,0.354,0,0,1,14.891,14.39ZM6.316,2.418a3.9,3.9,0,1,0,3.9,3.9A3.9,3.9,0,0,0,6.316,2.418Z"/></svg>');
  width: 100%;
  box-sizing: border-box;
  font-family: inherit;
  vertical-align: middle;
  height: 2.66667em;
  background-color: #fff;
  border: 1px solid rgba(17,17,19,.1);
  border-radius: 4px;
  box-shadow: none;
  padding: 0 8px;
  padding-left: 32px;
  color: rgba(17,17,19,.8);
  font-size: .9375em;
}

.live-search__input:focus {
  outline: inherit;
}

.live-search__list {
  width: 100% !important;
  margin: 0 !important;
  z-index: 99999;
  position: absolute;
  background: #FFF;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.1);
  list-style: none;
  padding: 0;
  margin: 0;
}

.live-search__list li {
  margin: 0;
  padding: 8px 8px 8px 4px;
  border-bottom: 1px solid #EEE;
}

.live-search__list li::after {
  display: block;
  content: ' ';
  clear: both;
}

.live-search__list__item__image {
  width: 80px;
  height: 80px;
  margin-right: 6px;
  overflow: hidden;
  float: left;
}

.live-search__list__item__image > img {
  width: 100%;
}

.live-search__list__item__price {
  font-weight: bold;
}

@media (max-width: 920px) {
  .live-search__container {
    width: 220px;
  }
  .live-search__list {
    width: 240px !important;
  }
}

@media (max-width: 856px) {
  .live-search__container {
    width: 140px;
  }
}

@media (max-width: 768px) {
  .live-search__container {
    width: 200px;
  }
}
